/**
 * Partial for adding standard breakpoint variables
 */
@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,700|Montserrat:400,500,600,700|Ubuntu+Mono:400,700|Inconsolata:400,700");
@media (min-width: 0) {
  .breakpoint-helper:after {
    content: ' ($xxs)';
  }
}

@media (min-width: 320px) {
  .breakpoint-helper:after {
    content: ' ($xs)';
  }
}

@media (min-width: 544px) {
  .breakpoint-helper:after {
    content: ' ($sm)';
  }
}

@media (min-width: 800px) {
  .breakpoint-helper:after {
    content: ' ($md)';
  }
}

@media (min-width: 960px) {
  .breakpoint-helper:after {
    content: ' ($lg)';
  }
}

@media (min-width: 1200px) {
  .breakpoint-helper:after {
    content: ' ($xl)';
  }
}

